import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import { teams } from "@/data/teamData";

export default function TeamPage() {
  const router = useRouter();
  const { id } = router.query;

  const [team, setTeam] = useState<any>(null);
  const [events, setEvents] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!id) return;

    const key = id.toString().toLowerCase();
    const t = teams[key] || {
      name: id.toString().toUpperCase(),
      logo: "/images/teams/default_logo.png",
      banner: "/images/teams/default_banner.jpg",
      colors: ["#000", "#555"],
    };

    // DUMMY EVENTS (replace with API later)
    const eventData = [
      {
        id: 1,
        title: `${t.name} vs Steelers`,
        date: "2025-01-18",
        venue: "AT&T Stadium",
        city: "Arlington, TX",
        price: 115,
      },
      {
        id: 2,
        title: `${t.name} vs Eagles`,
        date: "2025-02-04",
        venue: "Lincoln Financial Field",
        city: "Philadelphia, PA",
        price: 98,
      },
      {
        id: 3,
        title: `${t.name} vs Packers`,
        date: "2025-03-12",
        venue: "Lambeau Field",
        city: "Green Bay, WI",
        price: 140,
      }
    ];

    setTeam(t);
    setEvents(eventData);
    setLoading(false);
  }, [id]);

  if (!id || loading) {
    return (
      <div className="p-10 text-center text-gray-700 text-xl">
        Loading team…
      </div>
    );
  }

  return (
    <div className="p-6 space-y-8">

      {/* Banner */}
      <div
        className="w-full h-64 rounded-xl bg-cover bg-center shadow-lg"
        style={{
          backgroundImage: `url(${team.banner})`,
        }}
      />

      {/* Team Header */}
      <div className="flex items-center gap-6">
        <img
          src={team.logo}
          alt={team.name}
          className="w-24 h-24 rounded-full border-4 shadow-xl"
        />

        <div>
          <h1 className="text-4xl font-bold">{team.name}</h1>
        </div>
      </div>

      {/* Upcoming Events */}
      <div>
        <h2 className="text-3xl font-semibold mb-4">Upcoming Events</h2>

        <div className="grid gap-5">
          {events.map((ev) => (
            <div
              key={ev.id}
              className="bg-white rounded-xl p-5 shadow-md hover:shadow-lg border cursor-pointer transition"
            >
              <h3 className="text-xl font-semibold">{ev.title}</h3>

              <div className="text-gray-500 mt-1">
                {new Date(ev.date).toLocaleDateString("en-US", {
                  weekday: "short",
                  month: "short",
                  day: "numeric",
                  year: "numeric",
                })}
              </div>

              <div className="mt-1 text-gray-700">
                {ev.venue} • {ev.city}
              </div>

              <div className="mt-3 font-semibold text-indigo-600">
                Starting at ${ev.price}
              </div>
            </div>
          ))}
        </div>
      </div>

    </div>
  );
}
